$MainColor: #ff0000;

.red {
  color: $MainColor;
}

div.container {
  h4 {
    color: blue;
  }
  p {
    color: red;
  }
}

@mixin fuc() {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include fuc();
}

.my-alert2 {
  @extend .my-alert;
  background-color: green;
}

.my-alert p {
  color: blue;
  margin-bottom: 0;
}
